/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #fff;
  letter-spacing: 0.23rem;
}

@media (min-width: 1280px) {
  body {
    width: 100vw;
    height: 100vh;
  }
  .container {
    width: 100%;
    height: 100%;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
  }
  .container section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
  }
}
.contenedor {
  width: 90%;
  margin: 0px auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4 {
  color: #fff;
  font-family: "Orbitron", sans-serif;
  font-weight: 600;
  text-align: center;
  letter-spacing: 0.3rem;
  margin: 0;
}

label {
  color: #fff;
  letter-spacing: 0.2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 1.8rem;
}

.dark-mode {
  background-color: #333;
}

.glass {
  backdrop-filter: blur(1rem);
  border: 2px solid rgba(145, 143, 143, 0.25);
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
  background-color: rgba(145, 143, 143, 0.25);
  border-radius: 2rem;
}

@media (max-width: 480px) {
  p {
    font-size: 1.4rem;
  }
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.8rem;
  }
  h3 {
    font-size: 2.2rem;
  }
  h4 {
    font-size: 1.6rem;
  }
}
@media (max-width: 360px) {
  p {
    font-size: 1.2rem;
  }
  h1 {
    font-size: 2.8rem;
  }
  h2 {
    font-size: 2.6rem;
  }
  h3 {
    font-size: 2rem;
  }
  h4 {
    font-size: 1.4rem;
  }
}
@media (max-width: 320px) {
  p {
    font-size: 1rem;
  }
  h1 {
    font-size: 2.6rem;
  }
  h2 {
    font-size: 2.4rem;
  }
  h3 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.2rem;
  }
}
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.sombra {
  box-shadow: 0px 1px 15px 0px rgba(15, 15, 14, 0.9);
}

.red_sombra {
  box-shadow: 0px 1px 15px 0px rgba(135, 17, 17, 0.8);
}

.label-b {
  color: #000;
}

.boton {
  display: block;
  font-family: "Orbitron", sans-serif;
  color: #fff;
  text-align: center;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  border: none;
}
.boton:hover {
  cursor: pointer;
}

.pading-top {
  padding-top: 7rem;
}

.pading_top_2 {
  padding-top: 11rem;
}

/* background */
.bg {
  animation: slide 4.5s ease-in-out infinite alternate;
  background-image: linear-gradient(-60deg, #871111 50%, #333 50%);
  bottom: 0;
  left: -50%;
  opacity: 0.5;
  position: fixed;
  right: -50%;
  top: 0;
  z-index: -1;
}

.bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 5s;
}

.bg3 {
  animation-duration: 6s;
}

@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
.boton-edit {
  background-color: #646464;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.2rem;
}
.boton-edit:hover {
  background-color: #575757;
}

.boton-del {
  background-color: #871111;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.2rem;
}
.boton-del:hover {
  background-color: #700e0e;
}

.boton_secundario {
  background-color: #333;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.3rem;
}
.boton_secundario:hover {
  background-color: #262626;
}

.boton-formularios {
  background-color: #871111;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-formularios:hover {
  background-color: #700e0e;
}

/** Ui **/
.header {
  width: 100%;
  height: 80px;
  background-color: rgba(145, 143, 143, 0.25);
  backdrop-filter: blur(1rem);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 200;
  top: 0;
}
.header .logo {
  height: 80px;
  width: auto;
}
.header .logo img {
  max-height: 100%;
  max-width: 100%;
}

.icon_menu {
  width: 3rem;
  margin-right: 3rem;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
}
.icon_menu i {
  font-size: 2.7rem;
  cursor: pointer;
  position: absolute;
}

.menu {
  display: flex;
  justify-content: center;
  transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.options {
  display: flex;
}
.options .activ {
  background-color: #871111;
  border-radius: 0.5rem;
  backdrop-filter: blur(1rem);
  transition: color 300ms;
}
.options .activ .option p {
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
}
.options .option {
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
  align-items: center;
  position: relative;
  height: 4rem;
}
.options .option p {
  color: #fff;
  font-weight: 400;
  cursor: pointer;
  font-size: 2rem;
}
.options .option:hover {
  background-color: #871111;
  border: 0.1rem solid #333;
  border-radius: 0.5rem;
  backdrop-filter: blur(1rem);
  transition: color 300ms;
}
.options .option:hover p {
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
}

.usser p {
  font-size: 2.5rem;
  color: #fff;
  letter-spacing: 0.1rem;
}

@media (max-width: 810px) {
  .header {
    padding: 0 2rem;
  }
  .header .logo {
    height: 80px;
    width: 100%;
  }
  .header .logo img {
    max-height: 100%;
    max-width: 90%;
  }
  .header .option {
    padding: 0 0.7rem 0 0.7rem !important;
  }
  .header .option i, .header .option p {
    font-size: 1.5rem !important;
  }
}
@media (max-width: 480px) {
  .header {
    padding: 0 1rem !important;
  }
  .header .menu {
    height: 0vh;
    width: 100vw;
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    background: rgba(135, 17, 17, 0.63);
    backdrop-filter: blur(1rem);
    transition: 1.5s;
    overflow: hidden;
  }
  .header .menu .options {
    display: block;
    width: fit-content;
    margin: 25vh auto 0 auto;
    text-align: center;
  }
  .header .menu .options .activ {
    color: #918f8f;
  }
  .header .menu .options .activ .option {
    background-color: #333;
    border: 0.1rem solid #918f8f;
    border-radius: 0.5rem;
    backdrop-filter: blur(1rem);
    transition: color 300ms;
  }
  .header .menu .options .activ .option i, .header .menu .options .activ .option p {
    font-size: 2rem !important;
  }
  .header .menu .options .option {
    font-size: 5rem;
    width: 100%;
    margin-bottom: 2rem;
  }
  .header .menu .options .option:hover {
    color: #918f8f;
    transition: 0.5s;
    background-color: #333;
    border: 0.1rem solid #918f8f;
    border-radius: 0.5rem;
    backdrop-filter: blur(1rem);
    transition: color 300ms;
  }
  .header .menu .options .option:hover p {
    font-size: 2rem !important;
  }
  .header .active {
    opacity: 1;
    transition: 1.5s;
    height: 100vh;
  }
  .header .icon_menu {
    display: inline-flex;
  }
  .header .usser {
    display: none;
  }
}
@media (min-width: 1280px) {
  .header {
    padding: 0 5rem;
  }
}
